import React from 'react';
import axios from 'axios';
import SearchBar from './serchbar';
import ImageCard from './imagecard';
import './App.css'

class App extends React.Component {

// for the sake of term we used props

    state = {
        images : []
    }

    onSearchSubmit = async(term) => {
       const response = await axios.get('https://api.unsplash.com/search/photos' ,{
        params : { query : term },    
        headers : {
                Authorization: 'Client-ID nT_1cNBd1SBFJpZHkgvgsO5VOyRhzE_7e5F1QGoaMZg'
            }
        })
        this.setState({images : response.data.results})
    }

    render(){
        return(
    
        <div className="ui container" style={{marginTop:"10px"}}>
            <h1>Search <span>FaV</span> 10</h1>
            
            <SearchBar onSubmitProp = {this.onSearchSubmit}/>
            
             <h4>{this.state.images.length<1 ? 
             `${this.state.images.length} match is found ENTER SOME TEXT` :
              `${this.state.images.length} match(s) are found`}
            
             </h4>
            <ImageCard images = {this.state.images} />
        </div>
    )
 }    
}
export default App