import React from 'react';
import './imagecard.css'
import Image from './image'
const ImageCard = (props) => {
 
    return(
        <div className="image-card">
            {props.images.map( (image) => 
            <Image
            key={image.id} image={image} 
            /> 
              )}
            
        </div>
    )
}
export default ImageCard